export default {
    name: 'app-datetime-picker',
    props: {
        padding: {
            type: String,
            default: '0 24rpx'
        },
        value: {
            type: String,
            default: '0',
        },
        text: null,
        mode: {
            type: String,
            default: 'date',
        },
        start: {
            type: String,
            default: '',
        },
        end: {
            type: String,
            default: '',
        },
        fields: {
            type: String,
            default: 'day',
        },
        disabled: false,
        showArrow: {
            type: Boolean,
            default: true,
        },
        sign: {
            default: null,
        },
        background: {
            default: 'transparent',
        },
        showBorder: {
            default: false,
        },
        borderColor: {
            default: 'transparent',
        },
        height: {
            default: 80,
        },
        radius: {
            default: 0,
        },
        textColor: {
            default: '#666666',
        },
        textPosition: {
            default: 'right',
        },
        defaultValue: {
            type: String,
            default: '',
        }
    },
    data() {
        return {
            timeValue: 0
        }
    },
    created() {
        this.timeValue = this.value != 0 ? this.value : this.defaultValue
    },
    methods: {
        handleChange(e) {
            this.timeValue = e.detail.value
            this.$emit('input', e.detail.value, this.sign);
            this.$emit('change', e, this.sign);
        },
        handleCancel(e) {
            this.$emit('cancel', e.detail.value, this.sign);
        },
        handleClick(e) {
















        },
    },
}
